<script setup>
  import { onMounted } from 'vue';
  import { useRouter } from 'vue-router';
  import Footer from '@/components/core/Footer.vue';
  import Weather from '@/components/common/Weather.vue';
  import DangerRating from '@/components/common/DangerRating.vue';

  const router = useRouter();

  function redirect(target) {
    if (target === 'accommodation') {
      router.push('/accommodation');
    } else if (target === 'activities') {
      router.push('/explore/activities');
    } else if (target === 'trails') {
      router.push('/explore/trails');
    } else if (target === 'bird') {
      router.push('/explore/bird-watching');
    } else if (target === 'facilities') {
      router.push('/about/facilities');
    } else if (target === 'school') {
      router.push('/groups/school');
    } else {
      router.push('/');
    }
  }

  onMounted(() => {
    // eslint-disable-next-line global-require
    document.querySelector('#card-accommodation').style.setProperty('--background', `url(${require('@/assets/images/accommodation/deluxegarden/king1.jpeg')})`);
