    // eslint-disable-next-line global-require
    document.querySelector('#card-activities').style.setProperty('--background', `url(${require('@/assets/images/explore/activities2.jpg')})`);
    // eslint-disable-next-line global-require
    document.querySelector('#card-trails').style.setProperty('--background', `url(${require('@/assets/images/explore/10342489_10152144577461872_6101071351039110188_n.jpg')})`);
    // eslint-disable-next-line global-require
    document.querySelector('#card-bird').style.setProperty('--background', `url(${require('@/assets/images/explore/bird_watching.png')})`);
    // eslint-disable-next-line global-require
    document.querySelector('#card-bird').style.setProperty('--background-pos', '20% 0');
    // eslint-disable-next-line global-require
    document.querySelector('#card-facilities').style.setProperty('--background', `url(${require('@/assets/images/about/facilities.jpeg')})`);
    // eslint-disable-next-line global-require
    document.querySelector('#card-school').style.setProperty('--background', `url(${require('@/assets/images/groups/schools2.jpg')})`);
  });
</script>

